.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 1024px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  background-color: rgba(0, 0, 0, .1);
}

.menu-open {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

@media screen and (min-width: 1024px) {
  .menu-open {
    display: none;
  }
}

.menu-open:hover .menu-open__svg, .menu-open:focus .menu-open__svg {
  stroke: #fd9222;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #1e1823;
  padding: 32px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(50%);
}

.menu-container .menu-toggle {
  width: 28px;
  height: 28px;
  color: #fff;
  position: absolute;
  top: 20px;
  right: 54%;
}

.menu-container .menu-toggle:hover .menu-close__svg, .menu-container .menu-toggle:focus .menu-close__svg {
  stroke: #fd9222;
}

