.menu-open__svg {
  stroke: #111;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.menu-close__svg {
  stroke: #fff;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

