.footer {
  background-color: $main-color;
}
.footer__container-contact {
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 15%;
  }
  @media screen and (min-width: 1024px) {
    gap: 36%;
  }
  @media screen and (min-width: 1200px) {
    gap: 45%;
  }
  @media (min-width: 1201px) and (max-width: 2000px) {
    gap: 56%;
  }
}
.footer__subtitle {
  color: $white;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
  letter-spacing: -0.48px;
  padding-top: 32px;
  margin-left: 20px;
  margin-bottom: 32px;
  @media screen and (min-width: 768px) {
    padding-top: 50px;
    margin-left: 32px;
    margin-bottom: 54px;
    font-size: 28px;
    line-height: 1.21;
    letter-spacing: -0.56px;
  }
}
.footer__subtitle-span {
  color: $accent-color;
}
.footer__container {
  display: flex;
  gap: 113px;
  @media screen and (min-width: 768px) {
    padding-top: 50px;
    gap: 50px;
  }
}
.contact {
  display: flex;
  margin-left: 20px;
  margin-bottom: 32px;
}
.contact__list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.contact__link {
  color: $white;
  font-size: 14px;
  line-height: 1.28;
  letter-spacing: -0.28px;
}
.contact__text {
  color: $white;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  padding-bottom: 14px;
}
.soc__list {
  display: flex;
  gap: 14px;
  margin-right: 20px;
}
.footer__line {
  color: $white;
  opacity: 0.1;
  @media (min-width: 320px) and (max-width: 2000px) {
    width: 96%;
  }
}
.footer__list {
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding-bottom: 32px;
  padding-top: 32px;
  margin-left: 20px;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 24px;
    padding-left: 12px;
  }
}
.footer__link {
  color: $white;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  letter-spacing: -0.28px;
}
